.footerWrap {
    width: 100%;
    position: relative;
}

.footerRow {
    display: -ms-flexbox;
    display: flex;
    -ms-flex-direction: row;
        flex-direction: row;
    -ms-flex-pack: justify;
        justify-content: space-between;
    max-width: 1080px;
    padding: 10px 10px;
    margin: 0px auto;   
}

.footer {
    color: #11677d7e;
    font-size: 10pt;
    margin: 0;
}

.footer:hover {
    color: #11677d;
    cursor: none;
}

@media screen and (max-width: 720px){

    .footerRow {
        -ms-flex-direction: column;
            flex-direction: column;
        -ms-flex-pack: center;
            justify-content: center;
        text-align: center;
        padding: 10px;
    }
}


